.nounderline {
  text-decoration: none !important
}

#app-name {
	font-weight: bold;
}
.jumbotron {
	/*  Background fallback in case of IE8 & down, or in case the image doesn't load, such as with slower connections  */
	background: url("https://wallpaper-house.com/data/out/6/wallpaper2you_93109.jpg") center center no-repeat fixed #464646;

	font-family: Serif, Monospace;
	height: 400px;
	width: auto;
	opacity: 0.5;
}

h1, h4 {
	color: #ffffff;
	font-weight: bold;
}

.imageBlock {
	cursor: pointer;
	background: #AEAEAE;
	-webkit-transition: all .3s ease-in-out;
	transition: all .3s ease-in-out;
}

.imageBlock:hover {
	-webkit-transform: scale(1.1);
	        transform: scale(1.1); /* (110% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
	background: #F0F0F0;
}

.gameLost {
    /* Start the shake animation and make the animation last for 0.5 seconds */
    -webkit-animation: shake 1s;
            animation: shake 1s;
    /* When the animation is finished, start again */
    -webkit-animation-iteration-count: 2s;
            animation-iteration-count: 2s;
}

@-webkit-keyframes shake {
    0% { -webkit-transform: translate(1px, 1px) rotate(0deg); transform: translate(1px, 1px) rotate(0deg); }
    10% { -webkit-transform: translate(-1px, -2px) rotate(-1deg); transform: translate(-1px, -2px) rotate(-1deg); }
    20% { -webkit-transform: translate(-3px, 0px) rotate(1deg); transform: translate(-3px, 0px) rotate(1deg); }
    30% { -webkit-transform: translate(3px, 2px) rotate(0deg); transform: translate(3px, 2px) rotate(0deg); }
    40% { -webkit-transform: translate(1px, -1px) rotate(1deg); transform: translate(1px, -1px) rotate(1deg); }
    50% { -webkit-transform: translate(-1px, 2px) rotate(-1deg); transform: translate(-1px, 2px) rotate(-1deg); }
    60% { -webkit-transform: translate(-3px, 1px) rotate(0deg); transform: translate(-3px, 1px) rotate(0deg); }
    70% { -webkit-transform: translate(3px, 1px) rotate(-1deg); transform: translate(3px, 1px) rotate(-1deg); }
    80% { -webkit-transform: translate(-1px, -1px) rotate(1deg); transform: translate(-1px, -1px) rotate(1deg); }
    90% { -webkit-transform: translate(1px, 2px) rotate(0deg); transform: translate(1px, 2px) rotate(0deg); }
    100% { -webkit-transform: translate(1px, -2px) rotate(-1deg); transform: translate(1px, -2px) rotate(-1deg); }
}

@keyframes shake {
    0% { -webkit-transform: translate(1px, 1px) rotate(0deg); transform: translate(1px, 1px) rotate(0deg); }
    10% { -webkit-transform: translate(-1px, -2px) rotate(-1deg); transform: translate(-1px, -2px) rotate(-1deg); }
    20% { -webkit-transform: translate(-3px, 0px) rotate(1deg); transform: translate(-3px, 0px) rotate(1deg); }
    30% { -webkit-transform: translate(3px, 2px) rotate(0deg); transform: translate(3px, 2px) rotate(0deg); }
    40% { -webkit-transform: translate(1px, -1px) rotate(1deg); transform: translate(1px, -1px) rotate(1deg); }
    50% { -webkit-transform: translate(-1px, 2px) rotate(-1deg); transform: translate(-1px, 2px) rotate(-1deg); }
    60% { -webkit-transform: translate(-3px, 1px) rotate(0deg); transform: translate(-3px, 1px) rotate(0deg); }
    70% { -webkit-transform: translate(3px, 1px) rotate(-1deg); transform: translate(3px, 1px) rotate(-1deg); }
    80% { -webkit-transform: translate(-1px, -1px) rotate(1deg); transform: translate(-1px, -1px) rotate(1deg); }
    90% { -webkit-transform: translate(1px, 2px) rotate(0deg); transform: translate(1px, 2px) rotate(0deg); }
    100% { -webkit-transform: translate(1px, -2px) rotate(-1deg); transform: translate(1px, -2px) rotate(-1deg); }
}

.gameWon {
	-webkit-animation: rotate 1.5s;
	        animation: rotate 1.5s;
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
}

@-webkit-keyframes rotate {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    10% { -webkit-transform: rotate(36deg); transform: rotate(36deg); }
    20% { -webkit-transform: rotate(72deg); transform: rotate(72deg); }
    30% { -webkit-transform: rotate(108deg); transform: rotate(108deg); }
    40% { -webkit-transform: rotate(144deg); transform: rotate(144deg); }
    50% { -webkit-transform: rotate(180deg); transform: rotate(180deg); }
    60% { -webkit-transform: rotate(216deg); transform: rotate(216deg); }
    70% { -webkit-transform: rotate(252deg); transform: rotate(252deg); }
    80% { -webkit-transform: rotate(288deg); transform: rotate(288deg); }
    90% { -webkit-transform: rotate(324deg); transform: rotate(324deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes rotate {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    10% { -webkit-transform: rotate(36deg); transform: rotate(36deg); }
    20% { -webkit-transform: rotate(72deg); transform: rotate(72deg); }
    30% { -webkit-transform: rotate(108deg); transform: rotate(108deg); }
    40% { -webkit-transform: rotate(144deg); transform: rotate(144deg); }
    50% { -webkit-transform: rotate(180deg); transform: rotate(180deg); }
    60% { -webkit-transform: rotate(216deg); transform: rotate(216deg); }
    70% { -webkit-transform: rotate(252deg); transform: rotate(252deg); }
    80% { -webkit-transform: rotate(288deg); transform: rotate(288deg); }
    90% { -webkit-transform: rotate(324deg); transform: rotate(324deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

